import Vue from "vue";
import Router from "vue-router";
import Home from "@/views/Home.vue";
import BankFeed from "@/views/BankFeed.vue";

Vue.use(Router);

export default new Router({
  mode: "hash",
  routes: [
    {
      path: "/",
      name: "Home",
      component: Home
    },
    {
      path: "/bank-feed",
      name: "Bank Feed",
      component: BankFeed
    }/*,
    {
      path: "/web3",
      name: "Web Three",
      component: WebThree
    }*/
  ]
});
