<template>
  <div class="erc-1155">
    <!--<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 280"><path fill="#d3dde5" fill-opacity="1" d="M0,32L80,53.3C160,75,320,117,480,122.7C640,128,800,96,960,106.7C1120,117,1280,171,1360,197.3L1440,224L1440,0L1360,0C1280,0,1120,0,960,0C800,0,640,0,480,0C320,0,160,0,80,0L0,0Z"></path></svg>-->
      <div class="header">
        <h4>Merge Game</h4>
      </div>
      <div class="loader" :class="{ 'loading': loading }"></div>
      <div class="body">
        <div class="table_wrapper">
          <table>
            <tr>
              <th>Token</th>
              <th>Your Count</th>
              <th>Actions</th>
            </tr>
            <tr v-for="(line, i) in tableData" :key="i">
              <td>
                <div :class="line.classes" class="token">
                  <i class="fas fa-gem"></i>
                </div>
              </td>
              <td>{{ line.count }}</td>
              <td>
                <div v-if="connected" class="btn_section">
                  <div class="btn_wrapper">
                    <div v-if="i == 0" class="mg_btn small white" @click="mint">Mint</div>
                  </div>
                  <div class="btn_wrapper">
                    <div v-if="line.count > 1" class="mg_btn small white" @click="merge(line.id)">Merge</div>
                  </div>
                  <div class="btn_wrapper">
                    <div v-if="line.count > 0" class="mg_btn small white" @click="burn(line.id)">Burn</div>
                  </div>
                </div>
              </td>
            </tr>
          </table>
        </div>
        <div class="right_wrapper">
          <div class="right_selector">
            <h3 :class="{ 'selected': showInstructions }" @click="showInstructions = true">Instructions</h3>
            <h3 :class="{ 'selected': !showInstructions }" @click="showInstructions = false">About</h3>
          </div>
          <div class="instructions_wrapper" v-if="showInstructions">
            <div class="instruction" v-for="(p, i) in instructions" :key="i"><div>{{ i + 1 }}</div><p>{{ p }}</p></div>
          </div>
          <div class="about_wrapper" v-if="!showInstructions">
            <p>This game was the original inspiration behind this entire web resource. I had already developed smart contracts for ERC-20 and ERC-721 tokens and decided to dive into the world of ERC-1155. After developing this game, I decided to expand this website and turn it into a resource to help people better understand the blockchain.<br><br>
              Solidity Documentation outlines some of the capabilities of the ERC-1155 standard. If you're a developer or interested in learning more about Solidity, <span @click="goTo('https://docs.openzeppelin.com/contracts/3.x/erc1155')">this</span> is a great resource.<br><br>
              You may also be interested in seeing the Solidity code for the smart contract used by this page. Click <span @click="goTo('https://rinkeby.etherscan.io/address/0x3f33C199CD869E067d0A1e6E7Ba5C5825932D2a1')">here</span> to be taken to the Etherscan page for the contract.
            </p>
          </div>
          <div class="mg_info_line">
            <div class="float_wrapper">
              <i class="fa fa-info"></i>
              <h4>Enjoyed this resource? <span @click="goTo('https://www.twitter.com/dallin_stagg')">Follow me</span> on Twitter!</h4>
            </div>
          </div>  
        </div>
      </div>
      
    </div>
</template>

<script>
// import Breaker from "../components/Breaker.vue";
import json from '../assets/web3/ERC1155_abi.json';
import Web3 from 'web3'
export default {
  name: "ERC1155",
  data() {
    return {
      instructions: [
        "Verify that you have the Metamask extension set up, that you have a wallet loaded into your Metamask extension, and that you have a sufficient balance of Goerli ETH in that wallet. If you need help doing this, click on the question mark button in the upper left hand corner of the website for step by step instructions.",
        "Start by clicking the 'Mint' button on the first line of the table. This should open your Metamask extension and prompt you to mint an ERC-1155 token! After the transaction runs, your count of red tokens will increase by 1.",
        "Mint a few more times, because why not!?",
        "Try burning a token by clicking the burn button next to the token you want to burn. This will effectively destroy one token from that line on the table.",
        "Now try merging two tokens. The 'Merge' button appears when you have at least two of any given type of ERC-1155 token. The 'Merge' button will combine two of the tokens on that line and forge one of a higher tier!",
        "Keep going! The point of the game is to reach the rarist token color possible. How far can you get?"
      ],
      connected: false,
      network: '',
      constructedBatchQuery: {
        count: 10,
        addresses: [],
        ids: [],
        balances: []
      },
      loading: false,
      batchBalances: '',
      selectedAddress: '',
      contract: '',
      contractABI: json,
      showInstructions: true,
      titles: ["Bronze 1", "Bronze 2", "Bronze 3", "Silver 1", "Silver 2", "Silver 3", "Gold 1", "Gold 2", "Gold 3", "Degen"],
      classes: ["red", "orange", "yellow", "green", "blue", "mint", "purple", "bronze", "silver", "gold"]
    };
  },
  props: ['passedConnected', 'passedNetwork'],
  watch: {
    passedConnected() {
      if (this.passedConnected) {
        this.connected = true
        this.selectedAddress = window.ethereum.selectedAddress;
        this.initContract();
        
      } else {
        this.connected = false
        this.selectedAddress = ''
      }
    }
  },
  mounted() {
    if (this.passedConnected) {
      this.connected = true
      this.selectedAddress = window.ethereum.selectedAddress;
      this.initContract();
    } else {
      this.connected = false
      this.selectedAddress = ''
    }

  },
  methods: {
    initContract() {
      let web3 = new Web3(window.ethereum);
      let contractAddress = '0xC56598cb753dB6ecbfb37917001783ED4bB7AF61';
      let abi = this.contractABI;

      this.contract = new web3.eth.Contract(abi, contractAddress);
      
      this.contract.events.TransferSingle()
      .on('data', event => this.loadPage(event, 1))
      .on('changed', changed => this.loadPage(changed))

      this.constructBatchQuery();
    },
    handleResponse(variable, res) {
      this[variable] = res;
    },
    loadPage(event) {
      console.log(event);
      this.loading = false;
      this.balanceOfBatch();
    },
    balanceOfBatch() {
      this.contract.methods.balanceOfBatch(this.constructedBatchQuery.addresses, this.constructedBatchQuery.ids).call()
        .then(result => this.handleResponse('batchBalances', result)
        );
    
    },
    mint() {
      this.loading = true;
      this.contract.methods.mint().send({
        from: this.selectedAddress
      }).then(console.log("opening metamask")
      ).catch(()=>{
        this.loading = false;
      })
    },
    merge(id) {
      this.loading = true;
      this.contract.methods.merge(id).send({
        from: this.selectedAddress
      }).then(console.log("opening metamask")
      ).catch(()=>{
        this.loading = false;
      })
    },
    burn(id) {
      this.loading = true;
      this.contract.methods.burn(id).send({
        from: this.selectedAddress
      }).then(console.log("opening metamask")
      ).catch(()=>{
        this.loading = false;
      })
    },
    constructBatchQuery() {
      let addresses = []
      let ids = []
      for (var i = 1; i <= 10; i++) {
        addresses.push(this.selectedAddress);
        ids.push(i)
      }
      this.constructedBatchQuery.addresses = addresses
      this.constructedBatchQuery.ids = ids
      this.loadPage();
    },
    goTo(url) {
      window.open(url, '_blank');
    }
  },
  computed: {
    tableData() {
      let array = []
      for (var i = 0; i < this.titles.length; i++) {
        array.push({
          id: i + 1,
          tier: this.titles[i],
          classes: this.classes[i],
          count: this.batchBalances[i]
        })
      }
      return array;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/styles/global-styles.scss";
.erc-1155 {
  width: 100%;
  .header {
    padding-bottom: 1rem;
    h4 {
      color: $gray-dk;
      font-size: 2.5rem;
      margin: 1rem 0 .5rem;
      text-indent: 1rem;
    }
  }
  .loader {
    border: 4px solid $gray-border;
    border-bottom-color: transparent;
    border-radius: 1rem;
    height: 1.5rem;
    width: 1.5rem;
    position: absolute;
    top: 2rem;
    right: 2rem;
    animation-name: spin;
    animation-duration: 800ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear; 
    opacity: 0;
    transition: .1s ease-in-out opacity;
    &.loading {
      opacity: 1;
    }
  }
  @keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
  }
  .body {
    column-gap: 1rem;
    display: grid;
    align-items: stretch;
    justify-items: stretch;
    grid-template-columns: 3fr 2fr; 
    .table_wrapper {
      padding: 1rem;
      table {
        border-collapse: collapse;
        width: 100%;
        tr:not(:first-child) {
          border-top: 1px solid $gray-border; 
        }
        th, td {
          //text-indent: .5rem;
          text-align: left;
          line-height: 56px;
          .token {
            background-color: rgba($gray-dk, 0.9);
            border: 6px solid $gray-dk;
            border-radius: 1rem;
            margin: .5rem 0;
            width: 3rem;
            height: 3rem;
            text-align: center;
            text-indent: 0;
            line-height: 3rem;
            display: grid;
            align-items: stretch;
            justify-items: stretch;
            grid-template-columns: 1fr;
            &.bronze {
              border-color: #764E2E;
              background-color: rgba(#764E2E, 0.8);
            }
            &.silver {
              border-color: #636469;
              background-color: rgba(#636469, 0.8);
            }
            &.gold {
              border-color: #a88924;
              background-color: rgba(#a88924, 0.8);
            }
            &.red {
              border-color: $red;
              background-color: rgba($red, 0.8);
            }
            &.mint {
              border-color: $mint;
              background-color: rgba($mint, 0.8);
            }
            &.blue {
              border-color: $blue;
              background-color: rgba($blue, 0.8);
            }
            &.orange {
              border-color: $orange;
              background-color: rgba($orange, 0.8);
            }
            &.yellow {
              border-color: $yellow;
              background-color: rgba($yellow, 0.8);
            }
            &.green {
              border-color: $green;
              background-color: rgba($green, 0.8);
            }
            &.purple {
              border-color: $purple;
              background-color: rgba($purple, 0.8);
            }
            &.degen {
              border-color: $gray-dk;
              background-color: rgba($gray-dk, 0.8);
            }
            i {
              color: $gray-lt;
              filter: drop-shadow(1px 1px 2px rgba($gray-dk, .3));
              font-size: 1.5rem;
              line-height: 3rem;
            }
          }
          .mg_btn {
            line-height: 2rem;
          }
        }
        th:nth-child(3) {
          width: 8rem;
        }
        td:nth-child(3) {
          width: 8rem;
        }
        .btn_section {
          display: grid;
          align-items: stretch;
          justify-items: stretch;
          grid-template-columns: 1fr 1fr 1fr;
          column-gap: .5rem;
        }
      }
    }
    .right_wrapper {
      background-color: rgba($purple, 0.1);
      border-radius: .3rem;
      margin-top: 1rem;
      .right_selector {
        border-bottom: 2px solid rgba($purple-dk, 0.25);
        column-gap: .5rem;
        display: grid;
        align-items: stretch;
        justify-items: stretch;
        grid-template-columns: 1fr 1fr;
        margin: 1rem 3rem 1.5rem;
        padding: .5rem 2rem;
        h3 {
          border-radius: 4px;
          color: $gray-dk;
          cursor: pointer;
          font-size: 1rem;
          font-weight: 600;
          line-height: 2.25rem;
          margin: 0;
          text-align: center;
          transition: .1s ease-in-out background-color;
          &.selected {
            background-color: rgba($purple-dk, 0.15);
          }
        }
      }
      .instructions_wrapper {
        .instruction {
          column-gap: 1rem;
          display: grid;
          align-items: stretch;
          justify-items: stretch;
          grid-template-columns: 1.5rem 2fr;
          margin: 1rem;   
          div {
            //background: linear-gradient(-60deg, var(--ac-2), var(--ac-1));
            background-color: $purple;
            border-radius: 50%;
            color: $gray-lt;
            font-size: .9rem;
            height: 1.5rem;
            line-height: 1.5rem;
            text-align: center;
            width: 1.5rem;
          }
          p {
            color: $gray-dk;
            font-size: .95rem;
            margin: 0;
          }
        }
      }
      .about_wrapper {
        color: $gray-dk;
        margin: 1rem 3rem;  
        p {
          font-size: .95rem;
          margin: .2rem 0;
          span {
            color: $blue;
            cursor: pointer;
            text-decoration: underline;
          }
        }
      }
      
    }
  }
}

</style>
