<template>
  <div class="home_page">
    <!--<myNav :scroll="scrollY" @goToPortfolio="scrollTo('portfolio_section')" @goToContact="scrollTo('connect_section')" @goToAbout="scrollTo('about_section')"></myNav>-->
    <div class="haze mobile_only dark">
      <div>
        <p>This website isn't optimized for mobile, please use a desktop computer.</p>
        <div class="logo"></div>
      </div>
      
    </div>
    <Transition name="haze-fade">
      <div class="haze dark" v-if="showDisclaimer">
        <i class="fab fa-ethereum"></i>
        <div class="disclaimer">
          <p>By proceeding, you agree to the inherent risks of the blockchain and the custodial management of one's funds. You bear all responsibility for any lost funds, regardless of the reason of the loss. It is highly recommended that you create a brand new wallet for purposes of interacting with this website. Check the box below if you agree to these terms.</p>
          <div class="selector_section">
            <div class="selector" :class="{'checked': userAgrees}" @click="userAgrees = !userAgrees">
              <i class="fas fa-check"></i>
            </div>
            <p>I agree</p>
          </div>
          <div @click="closeDisclaimer" class="mg_btn small purple" style="width: 5rem;">
            Proceed
          </div>
        </div>
      </div>
    </Transition>
    <Transition name="haze-fade">
      <div class="haze" v-if="showInstructions" @click="showInstructions = false"></div>
    </Transition>
    <Transition name="slide-fade">
      <div id="instructions_section" v-show="showInstructions">
        <div class="instructions_wrapper">
          <h4>Instructions</h4>
          <div class="close_btn" @click="showInstructions = false">
            <i class="fas fa-times"></i>
          </div>
          <div class="text_wrapper">
            <p><b>1.</b> First, you'll want to make sure you have the Metamask extension set up on your Chrome browser. If you need to download Metamask, click <span @click="goTo('https://chrome.google.com/webstore/detail/metamask/nkbihfbeogaeaoehlefnkodbefgpgknn?hl=en')">here</span>.</p>
            <p><b>2.</b> Second, you'll need to make sure you have a wallet created and you're connected to the Goerli network. When installing Metamask for the first time, Metamask should automatically prompt you to create a wallet but connecting to the Goerli test network will not be automatic. To connect to Goerli, open Metamask and click the dropdown at the top. Find 'Goerli Test Network' and click on it to connect. If you do not see Goerli Test Network (or other test networks) you will need to enable test networks in your Metamask settings. Click <span @click="goTo('https://medium.com/@mwhc00/how-to-enable-ethereum-test-networks-on-metamask-again-d7831da23a09')">here</span> To learn how.</p>
            <p><b>3.</b> Third, you need to get some Goerli ETH from the Goerli faucet. Faucets are resources in Web3 that allow users to get some cryptocurrencies for testing purposes. This version of Ether (sometimes called Ethereum) is free from faucets but also worthless -- there is no point in getting more from the faucet than you need. Goerli ETH is the transactional token that allows us to execute code on the Goerli test network. You won't need much Goerli ETH, so you can probably just hit the faucet one time. You'll need your wallet address to use the faucet. To find it, open Metamask and look for a string of characters that starts with '0x', hover it, and click it to copy it to your clipboard. You can then paste that address into the faucet. You can access the faucet by clicking <span @click="goTo('https://goerlifaucet.com/')">here</span>.</p>
            <p><b>4.</b> Fourth, now that you have a wallet set up within Metamask with some Goerli ETH, you're ready to begin. If you haven't already, click the blue 'Connect Wallet' button in the upper right hand corner (you'll have to close out of this instructions view to see the button). When connecting for the first time, Metamask will open and ask you to verify that you trust this website. After connecting, you should see your wallet address take the place of the button.</p>
            <p><b>5.</b> Fifth, you're ready to interact with the smart contracts by minting and staking tokens! Each tab corresponds to a different type of Ethereum contract you can interact with. While on each page, be sure to read the contract specific instructions as well as the about section to make sure you're transacting correctly and can supervise the trasactions as they are processed on the Ethereum blockchain!</p>
          </div>
        </div>
        <img src="../assets/images/logo-white.png" alt="">
      </div>
    </Transition>
    <div id="website_body" :class="{ 'dark_theme': darkTheme }">
      <div class="left">
        <div class="help" @click="showInstructions = true">
          <i class="fa fa-question"></i>
        </div>
      </div>
      <div class="right">
        <div v-if="!connected" class="mg_btn connected_top" @click="connect">
          <div>Connect Wallet</div>
          <i class="fa fa-plug"></i>
        </div>
        <p v-else class="connected_top"><b>CONNECTED: </b><span @click="openEtherscan">{{ selectedAddress }}</span></p>
      </div>
      <div v-if="!connected" class="connect_prompt">
        <div class="wrapper">
          <h1>Welcome!</h1>
          <p>To use these resources you'll need to have a wallet set up in Metamask, have a small balance of Goerli ETH, and be connected to the Goerli Test Network. 
            If you don't know what any of that means or need help, click the question mark button in the upper left for instructions.
            When you have all of that ready, click the 'Connect Wallet' button in the upper right to get started!</p>
        </div>
      </div>
      <div class="selector" v-if="connected">
        <div @click="selectedContract = 1" :class="{ 'selected': selectedContract === 1 }">Token Staking</div>
        <div @click="selectedContract = 2" :class="{ 'selected': selectedContract === 2 }">NFT Minting</div>
        <div @click="selectedContract = 3" :class="{ 'selected': selectedContract === 3 }">Merge Game</div>
        <div class="slider" :class="{ 'first': selectedContract === 1, 'second': selectedContract === 2, 'third': selectedContract === 3, }"></div>
      </div>
      <div class="outer_box" v-if="connected">
        <div class="wrong_network_cover" v-if="connectedNetworkID !== 5">
          <div>
            <h1>Wrong Network!</h1>
            <p>It looks like the wallet you connected isn't currently connected to the Goerli network. To fix this, open Metamask, select the network dropdown at the top, and select Goerli. If you need more help, be sure to check out the setup instructions by clicking the question mark in the upper left hand corner of the page. <br><br>
            After you've connected to the Goerli network, please refresh the page and connect your wallet again.</p>
            <div class="mg_btn small purple" style="width: 7rem;" @click="reloadPage()">Refresh Page</div>
          </div>
        </div>
        <erc20 v-bind:passedConnected="connected" v-bind:passedNetwork="connectedNetworkID" v-show="selectedContract === 1 && connectedNetworkID === 5"></erc20>
        <erc721 v-bind:passedConnected="connected" v-bind:passedNetwork="connectedNetworkID" v-show="selectedContract === 2 && connectedNetworkID === 5"></erc721>
        <erc1155 v-bind:passedConnected="connected" v-bind:passedNetwork="connectedNetworkID" v-show="selectedContract === 3 && connectedNetworkID === 5"></erc1155>
      </div>
      <div class="footer">
        <div class="text_wrapper">
          <p>To support the creation of resources like this, send donations to 0xD980eea7E6ECA053cd390590d81b78612191234B and follow me on Twitter!</p>
        </div>
        <div class="twitter_wrapper">
          <div class="icon" @click="goTo('https://twitter.com/dallin_stagg')">
            <i class="fab fa-twitter"></i>
          </div>
        </div>
      </div>
    </div>
    
  </div>
</template>

<script>
import ERC20 from '../components/ERC20.vue';
import ERC721 from '../components/ERC721.vue';
import ERC1155 from '../components/ERC1155.vue';
import Web3 from 'web3'
export default {
  name: "Home",
  components: {
    erc20: ERC20,
    erc1155: ERC1155,
    erc721: ERC721
  },
  props: ['scrollY'],
  data() {
    return {
      connected: false,
      selectedAddress: '',
      selectedContract: 1,
      showInstructions: false,
      showDisclaimer: true,
      userAgrees: false,
      connectedNetworkID: ''
    };
  },
  computed: {    
    darkTheme() {
      return this.$store.state.darkTheme
    }
  },
  mounted() {
    
  },
  methods: {
    scrollTo(id) {
      var elmnt = document.getElementById(id);
      elmnt.scrollIntoView();
    },
    connect() {
      // this connects to the wallet
      if (window.ethereum) { // first we check if metamask is installed
        window.ethereum.request({ method: 'eth_requestAccounts' })
          .then(() => {
            this.connected = true; // If users successfully connected their wallet
            this.selectedAddress = window.ethereum.selectedAddress;

            let web3 = new Web3(window.ethereum);
            web3.eth.getChainId()
            .then(id => this.connectedNetworkID = id);

          });
        
        /* window.ethereum.on('networkChanged', function(networkId){
          this.connectedNetworkID = networkId
        }); */

      }
    },
    closeDisclaimer() {
      if (this.userAgrees) {
        this.showDisclaimer = false;
      }
    },
    openEtherscan() {
      window.open('https://goerli.etherscan.io/address/' + this.selectedAddress, '_blank');
    },
    goTo(url) {
      // https://rinkebyfaucet.com/
      // https://chrome.google.com/webstore/detail/metamask/nkbihfbeogaeaoehlefnkodbefgpgknn?hl=en
      window.open(url, '_blank');
    },
    reloadPage() {
      location.reload();
    }
  }
};
</script>
<style lang="scss">
@import "../assets/styles/global-styles.scss";
.haze {
  background-color: rgba(#0C0C0C, .95);
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0rem;
  z-index: 5;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr;
  &.mobile_only {
    display: none;
    z-index: 20;
    @include mobile {
      display: grid;
      p {
        color: $gray-lt;
        font-size: 1.2rem;
        margin: 0 1rem;
        text-align: center;
      }
      .logo {
        background: url('../assets/images/mini-logo-white.png');
        background-size: contain;
        height: 7rem;
        margin: 2rem auto;
        width: 7rem;
      }
    }
  }
  &.dark {
    background-color: #0C0C0C;
  }
  .fa-ethereum {
    color: rgba($gray-lt, .01);
    font-size: 110rem;
    transform: rotate(-10deg);
    position: absolute;
    z-index: 5;
  }
  .disclaimer {
    color: $gray-lt;
    width: 30rem;
    display: relative;
    z-index: 7;
    p {
      font-size: 1.2rem;
    }
    .selector_section {
      display: grid;
      align-items: center;
      justify-items: left;
      grid-template-columns: 3rem 1fr;
      margin: 1rem 0 1.5rem;
      .selector {
        background-color: $gray-lt;
        border-radius: 7px;
        cursor: pointer;
        line-height: 2rem;
        text-align: center;
        width: 2rem;
        height: 2rem;
        &.checked {
          background-color: $green;
          i {
            filter: drop-shadow(1px 1px 2px rgba($gray-dk, .63));
          }
          
        }
      }
      p {
        font-size: 1rem;
      }
    }
  }
  
}
.haze-fade-enter-active {
  transition: all 0.3s ease-out;
}

.haze-fade-leave-active {
  transition: all 0.3s ease;
}

.haze-fade-enter,
.haze-fade-leave-to {
  transform: translateX(-500px);
  opacity: 0;
}

::-webkit-scrollbar {
  width: .5rem;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #A1A4B0; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #7D839B; 
}

#instructions_section {
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 25rem 1fr;
  position: fixed;
  top: 1rem;
  left: 1rem;
  right: 1rem;
  bottom: 1rem;
  z-index: 6;
  .instructions_wrapper {
    background-color: $gray-lt;
    border-radius: .3rem;
    position: relative;
    h4 {
      background-color: $white;
      border-top-left-radius: .3rem;
      border-top-right-radius: .3rem;
      border-bottom: 1px solid $gray-border;
      box-shadow: 0 1px 5px 0 rgba(#0C0C0C, 0.05);
      font-size: 1.2rem;
      font-weight: 700;
      line-height: 3rem;
      margin: 0;
      text-indent: 1rem;
    }
    .close_btn {
      background-color: $gray-lt;
      border: 1px solid $gray-border;
      border-radius: 50%;
      color: $text-secondary;
      cursor: pointer;
      position: absolute;
      width: 2.2rem;
      height: 2.2rem;
      text-align: center;
      line-height: 2.2rem;
      font-size: 1.1rem;
      top: calc(.4rem - 1px);
      right: .7rem;
      transition: box-shadow .4s ease;
      i {
        transition: transform .4s ease;
      }
      &:hover {
        box-shadow: 0 1px 3px 0 rgba(#0C0C0C, 0.3);
        i {
          transform: rotate(90deg);
        }
      }
    }
    .text_wrapper {
      height: calc(100vh - 5rem - 1px);
      overflow: auto;
      padding: 0 1rem;
      p {
        span {
          color: $blue;
          cursor: pointer;
          text-decoration: underline;
          &:hover {
            color: $blue-dk;
          }
        }
      }
    }
  }
  img {
    width: 100%;
  }
}
.slide-fade-enter-active {
  transition: all 0.3s ease-out;
}

.slide-fade-leave-active {
  transition: all 0.3s ease;
}

.slide-fade-enter,
.slide-fade-leave-to {
  transform: translateX(-50px);
  opacity: 0;
}

#website_body {
  background-image: url("../assets/images/overlay-grid.svg");
  background-size: 100%;
  padding: 1rem 0 0;
  position: relative;
  .help {
    background-color: $gray-lt;
    box-shadow: 0 2px 8px 0 rgba(#0c0c0c, 0.7);
    border-radius: 50%;
    color: $text-primary;
    cursor: pointer;
    line-height: 3rem;
    position: absolute;
    top: 2.25rem;
    left: 2rem;
    text-align: center;
    width: 3rem;
    &:hover {
      animation: shake 0.5s;
    }
  }
  .connected_top {
    position: absolute;
    top: 1rem;
    right: 2rem;
    color: $gray-lt;
    span {
      cursor: pointer;
      &:hover {
        border-bottom: 2px solid $gray-lt;
      }
    }
  }
  h2 {
    color: $gray-lt;
    text-align: center;
    font-size: 4rem;
  }
  .connect_prompt {
    min-height: 100vh;
    display: grid;
    align-items: center;
    justify-items: center;
    grid-template-columns: 1fr;
    .wrapper {
      position: relative;
      top: -2rem;
      width: 35rem;
      h1 {
        color: $gray-lt;
        font-size: 2.5rem;
      }
      p {
        color: $gray-lt;
        font-size: 1.3rem;
      }
    }
    
  }
  .selector {
    background-color: $white;
    border-radius: 2rem;
    box-shadow: 0 1px 5px 0 rgba(#0C0C0C, 0.75);
    column-gap: .5rem;
    display: grid;
    align-items: stretch;
    justify-items: stretch;
    grid-template-columns: 1fr 1fr 1fr;
    padding: .5rem;
    margin: 8rem auto 5rem;
    position: relative;
    width: 40rem; 
    z-index: 1;
    div {
      color: $text-primary;
      border-radius: 1.5rem;
      cursor: pointer;
      font-weight: 700;
      line-height: 3rem;
      text-align: center;
      transition: .2s ease-in-out box-shadow;
      position: relative;
      z-index: 3;
      &:hover {
        box-shadow: 0 1px 5px 0 inset rgba($gray-dk, 0.25);
      }
       &.selected {
        &:hover {
          box-shadow: none;
        }
       }
    }
    .slider {
      background-color: $gray-border;
      box-shadow: 0 1px 2px 0 rgba($gray-dk, 0.5);
      height: 3rem;
      width: 13rem;
      position: absolute;
      top: .5rem;
      z-index: 2;
      transition: .2s ease-in-out left;
      &.first {
        left: .5rem;
      }
      &.second {
        left: 14rem;
      }
      &.third {
        left: 27.5rem;
      }
    }
  }
  .outer_box {
    background-color: $white;
    box-shadow: 0 2px 5px 0 rgba($gray-dk, 0.25),
        0 -18px 0px -8px rgba($white, 0.3), 0 -34px 0px -16px rgba($white, 0.2),
        0 -50px 0px -24px rgba($white, 0.1);
    box-sizing: border-box;
    margin: 2rem auto 5rem;
    max-width: calc(100% - 3rem);
    padding: 1.5rem;
    position: relative;
    width: 70rem;
    .wrong_network_cover {
      margin: 1rem 1rem 1.5rem;
      p {
        color: $text-secondary;
        margin-bottom: 2rem;
      }
    }
  }
}
.footer {
  background: #141414;
  color: $gray-lt;
  padding: 1rem 5rem;
  display: grid;
  align-items: center;
  justify-items: center;
  grid-template-columns: 1fr 5rem;
  user-select: text;
  .text_wrapper {
    margin: 0 auto;
    width: 40rem;
    max-width: 100%;
  }
  .twitter_wrapper {
    &:hover {
      animation: shake 0.5s;
    }
    .icon {
      background-color: $blue;
      border-radius: 50%;
      cursor: pointer;
      font-size: 1.4rem;
      height: 3rem;
      line-height: 3rem;
      text-align: center;
      width: 3rem;
      transition: .2s ease-out box-shadow;
    }
  }
}

@keyframes shake {
  0% { transform: rotate(0deg); }
  25% { transform: rotate(10deg); }
  50% { transform: rotate(-10deg); }
  75% { transform: rotate(10deg); }
  100% { transform: rotate(0deg); }
}
</style>
