<template>
  <div class="erc-721">
    <!--<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 280"><path fill="#d3dde5" fill-opacity="1" d="M0,32L80,53.3C160,75,320,117,480,122.7C640,128,800,96,960,106.7C1120,117,1280,171,1360,197.3L1440,224L1440,0L1360,0C1280,0,1120,0,960,0C800,0,640,0,480,0C320,0,160,0,80,0L0,0Z"></path></svg>-->
      <div class="header">
        <h4>NFT Minting</h4>
      </div>
      <div class="loader" :class="{ 'loading': loading }"></div>
      <div v-if="connected && mintedCount <= 25000" class="mg_btn green mint_top" @click="mint">
        <div>Mint</div>
        <i class="fa fa-plus"></i>
      </div>
      <div class="body">
        <div class="outer_wrapper">
          <div class="list_wrapper">
            <div class="item" v-for="(line, i) in tableData" :key="i">
              <img :src="getImageLink(line.id)" alt="">
              <h5 class="id"><span>Token ID:</span> {{ line.id }}</h5>
              <div class="hover_screen" @click="openOpenseaNFT(line.id)">
                <p>View On Opensea</p>
              </div>
            </div>
          </div>
        </div>
        <div class="right_wrapper">
          <div class="right_selector">
            <h3 :class="{ 'selected': showInstructions }" @click="showInstructions = true">Instructions</h3>
            <h3 :class="{ 'selected': !showInstructions }" @click="showInstructions = false">About</h3>
          </div>
          <div class="instructions_wrapper" v-if="showInstructions">
            <div class="instruction" v-for="(p, i) in instructions" :key="i"><div>{{ i + 1 }}</div><p>{{ p }}</p></div>
          </div>
          <div class="about_wrapper" v-if="!showInstructions">
            <p>The NFT Minting page was made possible by NFT Chat and their NFT collection, Colonists. The NFTs minted on this website have no affiliation with their project, nor do they provide any inherent utility...but their team was kind enough to let us use their PFP images to illustrate the minting concept! <br><br>
              Click <span @click="goTo('https://www.colony.online/')">here</span> to view their website or click <span @click="goTo('https://opensea.io/collection/colonists')">here</span> to purchase a Colonist that will be usable in their upcoming social metaverse project that is currently in development! <br><br>
              To view the Opensea collection of this smart contract click <span @click="goTo('https://testnets.opensea.io/collection/dallin-stagg')">here</span> or click <span @click="goTo('https://goerli.etherscan.io/address/0x4276646a8aab2191ae91341ecc4e944e2d54010a')">here</span> to see the Etherscan contract listing.<br><br>
              Developers or others interested in the code running behind the scenes for all Ethereum NFT projects can access the OpenZeppelin ERC-721 token standard documentation by clicking <span @click="goTo('https://docs.openzeppelin.com/contracts/4.x/erc721')">here</span>.
            </p>
          </div>
          <div class="mg_info_line">
            <div class="float_wrapper">
              <i class="fa fa-info"></i>
              <h4>Enjoyed this resource? <span @click="goTo('https://www.twitter.com/dallin_stagg')">Follow me</span> on Twitter!</h4>
            </div>
          </div>  
        </div>
      </div>
      
    </div>
</template>

<script>
// import Breaker from "../components/Breaker.vue";
// PURE GOLD FOR EVENTS
// https://www.coinclarified.com/p/3-ways-to-subscribe-to-events-with-web3-js/
import json from '../assets/web3/ERC721_abi.json';
import Web3 from 'web3'
export default {
  name: "ERC721",
  data() {
    return {
      instructions: [
        "Verify that you have the Metamask extension set up, that you have a wallet loaded into your Metamask extension, and that you have a sufficient balance of Goerli ETH in that wallet. If you need help doing this, click on the question mark button in the upper left hand corner of the website for step by step instructions.",
        "Welcome to NFT Minting! Here, you can practice minting ERC-721 tokens!",
        "The table contains up to the last ten ERC-721 tokens minted using this smart contract. To create a new one and send it to your wallet, try pressing the 'Mint' button. After the transaction runs you will see your new NFT appear.",
        "Be sure to check out the about section for more information on where these awesome NFT pictures come from!"
      ],
      connected: false,
      selectedAddress: '',
      contract: '',
      contractABI: json,
      mintedCount: 0,
      displayStart: 1,
      showInstructions: true,
      openseaLink: 'https://testnets.opensea.io/assets/goerli/0x4276646a8aab2191ae91341ecc4e944e2d54010a/',
      etherscanLink: '',
      loading: false
    };
  },
  props: ['passedConnected', 'passedNetwork'],
  watch: {
    passedConnected() {
      if (this.passedConnected) {
        this.connected = true
        this.selectedAddress = window.ethereum.selectedAddress;
        this.initContract();
      } else {
        this.connected = false
        this.selectedAddress = ''
      }
    }
  },
  mounted() {
    if (this.passedConnected) {
      this.connected = true
      this.selectedAddress = window.ethereum.selectedAddress;
      this.initContract();
    } else {
      this.connected = false
      this.selectedAddress = ''
    }

    this.contract.events.Transfer()
    .on('data', event => this.loadPage(event))
    .on('changed', changed => this.loadPage(changed))

  },
  methods: {
    initContract() {
      let web3 = new Web3(window.ethereum);
      let contractAddress = '0x4276646A8aab2191ae91341ecc4E944e2D54010A';
      let abi = this.contractABI;

      this.contract = new web3.eth.Contract(abi, contractAddress);
      this.loadPage();
    },
    handleResponse(variable, res) {
      this[variable] = res;
    },
    loadPage(event) {
      console.log(event)
      this.loading = false;
      this.mintedQuantity();
    },
    mintedQuantity() {
      this.contract.methods.getMintedQuantity().call()
        .then(result => { 
          this.handleResponse('mintedCount', parseInt(result))
          if (parseInt(result) <= 10) {
            this.displayStart = 1
          } else {
            this.displayStart = parseInt(result) - 9
          }
      });
    },
    mint() {
      this.loading = true;
      this.contract.methods.mint().send({
        from: this.selectedAddress
      }).then(console.log("opening metamask")
      ).catch(()=>{
        this.loading = false;
      })
    },
    getImageLink(id) {
      return "https://mint.colony.online/3d-colonists/" + id + ".png"
    },
    openOpenseaNFT(id) {
      window.open(this.openseaLink + id, '_blank');
    },
    openEtherscan(wallet) {
      window.open('https://goerli.etherscan.io/address/' + wallet, '_blank');
    },
    goTo(url) {
      window.open(url, '_blank');
    }
  },
  computed: {
    tableData() {
      let array = []
      for (var i = this.mintedCount; i >= this.displayStart; i--) {
        array.push({'id': i})
      }
      return array;
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/styles/global-styles.scss";
.erc-721 {
  color: $text-primary;
  width: 100%;
  .header {
    padding-bottom: 2rem;
    h4 {
      color: $gray-dk;
      font-size: 2.5rem;
      margin: 1rem 0 .5rem;
      text-indent: 1rem;
    }
  }
  .loader {
    border: 4px solid $gray-border;
    border-bottom-color: transparent;
    border-radius: 1rem;
    height: 1.5rem;
    width: 1.5rem;
    position: absolute;
    top: 2.8rem;
    right: 15rem;
    animation-name: spin;
    animation-duration: 800ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear; 
    opacity: 0;
    transition: .1s ease-in-out opacity;
    &.loading {
      opacity: 1;
    }
  }
  @keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
  }
  .body {
    column-gap: 1rem;
    display: grid;
    align-items: stretch;
    justify-items: stretch;
    grid-template-columns: 3fr 2fr; 
    .outer_wrapper {
      padding: 0 1rem;

      .list_wrapper {
        display: grid;
        align-items: stretch;
        justify-items: stretch;
        position: relative;
        grid-template-columns: 1fr 1fr; 
        column-gap: 1rem;
        row-gap: 1rem;
        .item {
          background-color: $white;
          box-shadow: 0 2px 5px 0 rgba($gray-dk, 0.15);
          position: relative;
          padding: .5rem .5rem 0;
          overflow: hidden;
          &:hover {
            .hover_screen {
              transform: translateY(0rem);
            }
          }
          img {
            width: 100%;
          }
          h5 {
            background-color: $gray-dk;
            border-radius: 1rem;
            color: $gray-lt;
            font-size: .75rem;
            font-weight: 400;
            position: absolute;
            top: 1rem;
            right: 1rem;
            padding: 0 1rem;
            line-height: 1.8rem;
            margin: 0;
            text-align: center;
          }
          .hover_screen {
            background-color: $gray-dk;
            border-radius: 1rem;
            box-shadow: 0 2px 6px 2px rgba($gray-dk, 0.25);
            cursor: pointer;
            position: absolute;
            height: 4rem;
            left: 1rem;
            right: 1rem;
            bottom: 1rem;
            transform: translateY(7rem);
            transition: .3s ease-out transform;
            display: grid;
            align-items: center;
            justify-items: center;
            grid-template-columns: 1fr; 
            p {
              color: $gray-lt;
            }
            &:hover {
              p {
                animation: sidetoside 0.5s;
                color: $white;
              }
            }
          }
          .mg_btn {
            margin-top: 1.5rem;
            width: 10rem;
          }
        }
      }
    }
    .right_wrapper {
      background-color: rgba($purple, 0.1);
      border-radius: .3rem;
      .right_selector {
        border-bottom: 2px solid rgba($purple-dk, 0.25);
        column-gap: .5rem;
        display: grid;
        align-items: stretch;
        justify-items: stretch;
        grid-template-columns: 1fr 1fr;
        margin: 1rem 3rem 1.5rem;
        padding: .5rem 2rem;
        h3 {
          border-radius: 4px;
          color: $gray-dk;
          cursor: pointer;
          font-size: 1rem;
          font-weight: 600;
          line-height: 2.25rem;
          margin: 0;
          text-align: center;
          transition: .1s ease-in-out background-color;
          &.selected {
            background-color: rgba($purple-dk, 0.15);
          }
        }
      }
      .instructions_wrapper {
        .instruction {
          column-gap: 1rem;
          display: grid;
          align-items: stretch;
          justify-items: stretch;
          grid-template-columns: 2rem 2fr;
          margin: 1rem;   
          div {
            //background: linear-gradient(-60deg, var(--ac-2), var(--ac-1));
            background-color: $purple;
            border-radius: 50%;
            color: $gray-lt;
            font-size: .9rem;
            height: 1.5rem;
            line-height: 1.5rem;
            text-align: center;
            width: 1.5rem;
          }
          p {
            color: $gray-dk;
            font-size: .95rem;
            margin: 0;
          }
        }
      }
      .about_wrapper {
        color: $gray-dk;
        margin: 1rem 3rem;  
        p {
          margin: .2rem 0;
          font-size: .95rem;
          span {
            color: $blue;
            cursor: pointer;
            text-decoration: underline;
          }
        }
      }
      
    }
  }
  .mint_top {
    position: absolute;
    top: .75rem;
    right: 2rem;
  }
  
}

</style>
