<template>
  <div id="erc-20">
    <!--<svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 280"><path fill="#d3dde5" fill-opacity="1" d="M0,32L80,53.3C160,75,320,117,480,122.7C640,128,800,96,960,106.7C1120,117,1280,171,1360,197.3L1440,224L1440,0L1360,0C1280,0,1120,0,960,0C800,0,640,0,480,0C320,0,160,0,80,0L0,0Z"></path></svg>-->
      <div class="header">
        <h4>Token Staking</h4>
      </div>
      <div class="loader" :class="{ 'loading': loading }"></div>
      <div class="body">
        <div class="outer_wrapper">
          <div class="list_wrapper">
            <div class="approval_dropdown" v-if="allowance < 1000000000">
              <p>
                To interact with this page you'll need to approve the smart contract that manages the staking and rewards tokens and fascilitates the staking process. <br><br>
                <b>Why are approvals necessary and what is their purpose?</b><br><br>
                Approving a smart contract allows that contract to move your assets without needing any further permission. This seems like a security hazard but is actually an increadibly important part of the Web3 space and fascilitates asset purchases and transfers. Consider the example of you selling a digital asset like an NFT on an exchange. You put the asset up for sale with the price of your choosing. When someone tries to buy the asset you've listed they are unable to because that asset is locked in your wallet and cannot be moved without your permission. Here, when you list your asset, you will approve a smart contract that is decentralized and managed by the exchange. When that smart contract is given the appropriate amount of ETH for your asset, it will automatically transfer the asset for you using your approval you already gave.<br><br>
                This page utilizes approval functionality to fascilitate the staking process. You approve the smart contract that manages the tokens so that when you "deposit" your token, that contract can move your assets for you and keep a record with them with an accurate token reward count.
              </p>
              <div class="mg_btn green small" @click="setApproval">Approve Contract</div>
            </div>
            <div class="item">
              <div class="coin_wrapper">
                <div class="coin green">
                  <div class="logo"></div>
                </div>
                <div class="name_wrapper">
                  <h4>Staking Token</h4>
                  <h5>Total: {{ parseInt(unstakedBalance) + parseInt(stakedBalance) }}</h5>
                </div>
                <div class="mg_btn green small" @click="mint">Mint</div>
              </div>
              <div class="coin_body">
                <div class="section">
                  <h3>Unstaked</h3>
                  <h2>{{ unstakedBalance }}</h2>
                  <div class="input_wrapper">
                    <input type="number" :class="{ 'invalid': ((parseInt(amountToStake) < 1 || parseInt(amountToStake) > parseInt(unstakedBalance)) && this.amountToStake !== '') }" v-model="amountToStake">
                    <div class="mg_btn purple small" @click="stake">Stake</div>
                  </div>
                </div>
                <div class="section">
                  <h3>Staked</h3>
                  <h2>{{ stakedBalance }}</h2>
                  <div class="input_wrapper">
                    <input type="number" :class="{ 'invalid': ((parseInt(amountToUnstake) < 1 || parseInt(amountToUnstake) > parseInt(stakedBalance)) && this.amountToUnstake !== '') }" v-model="amountToUnstake">
                    <div class="mg_btn purple small lil_wider" @click="unstake">Unstake</div>
                  </div>
                </div>
              </div>
              <div class="mg_btn etherscan_btn small white" @click="goTo('https://goerli.etherscan.io/address/0xBCe1D355ac581F793817ddD5C252bD25c4C6259b')">View On Etherscan</div>
            </div>
            <div class="item">
              <div class="coin_wrapper">
                <div class="coin orange">
                  <div class="logo"></div>
                </div>
                <div class="name_wrapper">
                  <h4>Reward Token</h4>
                  <h5>Total: {{ parseInt(claimableBalance) + parseInt(claimedBalance) }}</h5>
                </div>
                <h4 class="apr">{{ rewardRate }}% APR</h4>
              </div>
              <div class="coin_body">
                <div class="section">
                  <h3>Earned and Claimable</h3>
                  <h2>{{ claimableBalance }}</h2>
                  <div class="btn_wrapper">
                    <div class="mg_btn purple small" @click="claim">Claim Rewards</div>
                    <div class="mg_btn small white" @click="getClaimableBalance">Refresh Rewards</div>
                  </div>
                </div>
                <div class="section">
                  <h3>Claimed</h3>
                  <h2>{{ claimedBalance }}</h2>
                </div>
              </div>
              <div class="mg_btn etherscan_btn small white" @click="goTo('https://goerli.etherscan.io/address/0xDec49731953658E6b1b2c5E622Fd5c8a2f700d16')">View On Etherscan</div>
            </div>
          </div>
        </div>
        <div class="right_wrapper">
          <div class="right_selector">
            <h3 :class="{ 'selected': showInstructions }" @click="showInstructions = true">Instructions</h3>
            <h3 :class="{ 'selected': !showInstructions }" @click="showInstructions = false">About</h3>
          </div>
          <div class="instructions_wrapper" v-if="showInstructions">
            <div class="instruction" v-for="(p, i) in instructions" :key="i"><div>{{ i + 1 }}</div><p>{{ p }}</p></div>
          </div>
          <div class="about_wrapper" v-if="!showInstructions">
            <p>This is personally my favorite of the three pages. It incorporates three smart contracts instead of just one like the other two pages.<br><br>
              <b>Staking Contract: </b>The staking contract has ERC-20 functionality and lets you mint these tokens for free. Not much more is included here. Just all the standard ERC-20 functionality and a public mint function. To view the staking token smart contract, click <span @click="goTo('https://goerli.etherscan.io/address/0xBCe1D355ac581F793817ddD5C252bD25c4C6259b')">here</span>.<br><br>
              <b>Rewards Contract: </b>The rewards contract is a little different from the staking contract. Although it is also built on the ERC-20 token standard, users are not able to mint this token unless they are the contract owner. This means that the only way for users to get this token is through the staking process. To view the rewards token smart contract, click <span @click="goTo('https://goerli.etherscan.io/address/0xDec49731953658E6b1b2c5E622Fd5c8a2f700d16')">here</span>.<br><br>
              <b>Interface Contract: </b>The staking interface contract is where all the magic happens. This contract does not create an ERC-20 token, but uses what are called IERC20 interfaces to manage the staking and rewards tokens. The majority of all functionality is done here. Staking, withdrawals, APR/rewards calculations, rewards claims etc., are all done in this smart contract. When you desire to stake the staking token, you'd think you would be interacting with the staking token smart contract. What is actually happening is you're interacting with the interface contract which tells the staking contract to transfer your tokens from your wallet to the interface contract for safe keeping. When you withdraw, the interface sends those tokens back to you and updates your rewards. To view the interface smart contract click <span @click="goTo('https://goerli.etherscan.io/address/0xab203eec1a192cdbe4ac3d02893b2a8a315a8c7a')">here</span>. <br><br>
              The key resource to be aware of here is OpenZeppelin's documentation of the ERC-20 token standard. If you're a developer or interested in learning more about Solidity, <span @click="goTo('https://docs.openzeppelin.com/contracts/4.x/erc20')">this</span> is a great resource to understand the core functionality that all ERC-20 tokens inherit.
            </p>
          </div>
          <div class="mg_info_line">
            <div class="float_wrapper">
              <i class="fa fa-info"></i>
              <h4>Enjoyed this resource? <span @click="goTo('https://www.twitter.com/dallin_stagg')">Follow me</span> on Twitter!</h4>
            </div>
          </div>  
        </div>
      </div>
      
    </div>
</template>

<script>
// import Breaker from "../components/Breaker.vue";
// PURE GOLD FOR EVENTS
// https://www.coinclarified.com/p/3-ways-to-subscribe-to-events-with-web3-js/
import stakingJson from '../assets/web3/ERC20_staking_abi.json';
import rewardsJson from '../assets/web3/ERC20_rewards_abi.json';
import interfaceJson from '../assets/web3/ERC20_interface_abi.json';
import Web3 from 'web3'
export default {
  name: "ERC20",
  data() {
    return {
      instructions: [
        "Verify that you have the Metamask extension set up, that you have a wallet loaded into your Metamask extension, and that you have a sufficient balance of Goerli ETH in that wallet. If you need help doing this, click on the question mark button in the upper left hand corner of the website for step by step instructions.",
        "Before being able to mint or stake the tokens displayed on this page you'll have to approve the smart contract that manages both tokens. If you have not approved the contract yet, instructions to do so will be displayed to the left.",
        "After approving the contract, start by clicking the 'Mint' button. This will increase your Unstaked Staking Token balance by 1000000 after the transaction runs!",
        "You can now stake your Staking tokens. Choose an amount you would like to stake by typing that number into the text box in the 'Unstaked' section and click Stake. Approve the transaction.",
        "You'll see that your Unstaked and Staked balances will adjust to accomodate the staking taking place.",
        "From here, you can monitor your reward tokens accumulating based on the amount of Staking Tokens that are staked and the contract's yearly APR. Your earned balance will automatically update any time you complete a staking or unstaking action, or to simply refresh your claimable rewards balance, click the 'Refresh Rewards' button.",
        "To claim accumulated rewards from the managing contract and have them sent to your wallet, click the 'Claim Rewards' button.",
        "That's it! Be sure to check the about section in this left panel. Read through and click the resource links to watch the transactions happening on the blockchain in real time!"
      ],
      connected: false,
      selectedAddress: '',
      stakingContract: '',
      stakingContractAddress: '0xBCe1D355ac581F793817ddD5C252bD25c4C6259b',
      stakingContractABI: stakingJson,
      rewardsContract: '',
      rewardsContractAddress: '0xDec49731953658E6b1b2c5E622Fd5c8a2f700d16',
      rewardsContractABI: rewardsJson,
      interfaceContract: '',
      interfaceContractAddress: '0xaB203eec1A192cdBE4ac3D02893b2A8A315A8C7A',
      interfaceContractABI: interfaceJson,
      unstakedBalance: 0,
      stakedBalance: 0,
      claimableBalance: 0,
      claimedBalance: 0,
      allowance: 0,
      rewardRate: 0,
      amountToStake: '',
      amountToUnstake: '',
      showInstructions: true,
      loading: false,
      openseaLink: 'https://testnets.opensea.io/assets/rinkeby/0xdbeac639bcb1283188f16568aff39bbc868937f9/',
      etherscanLink: ''
    };
  },
  props: ['passedConnected', 'passedNetwork'],
  watch: {
    passedConnected() {
      if (this.passedConnected) {
        this.connected = true
        this.selectedAddress = window.ethereum.selectedAddress;
        this.initContract();
      } else {
        this.connected = false
        this.selectedAddress = ''
      }
    }
  },
  mounted() {
    if (this.passedConnected) {
      this.connected = true
      this.selectedAddress = window.ethereum.selectedAddress;
      this.initContract();
    } else {
      this.connected = false
      this.selectedAddress = ''
    }

    this.stakingContract.events.Transfer()
    .on('data', event => this.loadPage(event))
    .on('changed', changed => this.loadPage(changed))

    this.stakingContract.events.Approval()
    .on('data', event => this.loadPage(event))
    .on('changed', changed => this.loadPage(changed))

    this.rewardsContract.events.Transfer()
    .on('data', event => this.loadPage(event))
    .on('changed', changed => this.loadPage(changed))
    
  },
  methods: {
    initContract() {
      let web3 = new Web3(window.ethereum);

      this.stakingContract = new web3.eth.Contract(this.stakingContractABI, this.stakingContractAddress);
      this.rewardsContract = new web3.eth.Contract(this.rewardsContractABI, this.rewardsContractAddress);
      this.interfaceContract = new web3.eth.Contract(this.interfaceContractABI, this.interfaceContractAddress);
      this.loadPage();
    },
    handleResponse(variable, res) {
      this[variable] = res;
    },
    loadPage(event) {
        console.log(event)
        this.loading = false;
        this.amountToStake = '';
        this.amountToUnstake = '';
        this.getUnstakedBalance();
        this.getStakedBalance();
        this.getClaimableBalance();
        this.getClaimedBalance();
        this.getRewardRate();
        this.getAllowance();
      
    },
    getUnstakedBalance() {
      this.stakingContract.methods.balanceOf(this.selectedAddress).call()
        .then(result => { 
          this.handleResponse('unstakedBalance', result)
      });
    },
    getStakedBalance() {
      this.interfaceContract.methods.balanceOf(this.selectedAddress).call()
        .then(result => { 
          this.handleResponse('stakedBalance', result)
      });
    },
    getClaimableBalance() {
      this.interfaceContract.methods.earned().call({
        from: this.selectedAddress
      })
        .then(result => { 
          console.log(result)
          this.handleResponse('claimableBalance', result)
      });
    },
    getClaimedBalance() {
      this.rewardsContract.methods.balanceOf(this.selectedAddress).call()
        .then(result => { 
          this.handleResponse('claimedBalance', result)
      });
    },
    getAllowance() {
      this.stakingContract.methods.allowance(this.selectedAddress, this.interfaceContractAddress).call()
        .then(result => { 
          this.handleResponse('allowance', result)
      });
    },
    getRewardRate() {
      this.interfaceContract.methods.rewardRate().call()
        .then(result => { 
          this.handleResponse('rewardRate', result)
      });
    },
    setApproval() {
      this.loading = true;
      this.stakingContract.methods.approve(this.interfaceContractAddress, 1000000000000000).send({
        from: this.selectedAddress
      }).then(console.log("opening metamask")
      ).catch(()=>{
        this.loading = false;
      })
    },
    mint() {
      this.loading = true;
      this.stakingContract.methods.mint().send({
        from: this.selectedAddress
      }).then(console.log("opening metamask")
      ).catch(()=>{
        this.loading = false;
      })
    },
    claim() {
      this.loading = true;
      this.interfaceContract.methods.claimReward().send({
        from: this.selectedAddress
      }).then(console.log("opening metamask")
      ).catch(()=>{
        this.loading = false;
      })
    },
    stake() {
      if (parseInt(this.amountToStake) > 0 && parseInt(this.amountToStake) <= parseInt(this.unstakedBalance)) {
        this.loading = true;
        this.interfaceContract.methods.stake(this.amountToStake).send({
          from: this.selectedAddress
        }).then(console.log("opening metamask")
        ).catch(()=>{
          this.loading = false;
        })
      }
    },
    unstake() {
      if (parseInt(this.amountToUnstake) > 0 && parseInt(this.amountToUnstake) <= parseInt(this.stakedBalance)) {
        this.loading = true;
        this.interfaceContract.methods.withdraw(this.amountToUnstake).send({
          from: this.selectedAddress
        }).then(console.log("opening metamask")
        ).catch(()=>{
          this.loading = false;
        })
      }
    },
    getImageLink(id) {
      return "https://mint.colony.online/3d-colonists/" + id + ".png"
    },
    openEtherscan(wallet) {
      window.open('https://goerli.etherscan.io/address/' + wallet, '_blank');
    },
    goTo(url) {
      window.open(url, '_blank');
    }
  }
};
</script>
<style lang="scss" scoped>
@import "../assets/styles/global-styles.scss";
#erc-20 {
  color: $text-primary;
  width: 100%;
  .header {
    padding-bottom: 1rem;
    h4 {
      color: $gray-dk;
      font-size: 2.5rem;
      margin: 1rem 0 .5rem;
      text-indent: 1rem;
    }
  }
  .loader {
    border: 4px solid $gray-border;
    border-bottom-color: transparent;
    border-radius: 1rem;
    height: 1.5rem;
    width: 1.5rem;
    position: absolute;
    top: 2rem;
    right: 2rem;
    animation-name: spin;
    animation-duration: 800ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear; 
    opacity: 0;
    transition: .1s ease-in-out opacity;
    &.loading {
      opacity: 1;
    }
  }
  @keyframes spin {
    from {
        transform:rotate(0deg);
    }
    to {
        transform:rotate(360deg);
    }
  }
  .body {
    column-gap: 1rem;
    display: grid;
    align-items: stretch;
    justify-items: stretch;
    grid-template-columns: 2fr 1fr; 
    position: relative;
    .outer_wrapper {
      padding: 0 1rem;

      .list_wrapper {
        position: relative;
        .approval_dropdown {
          background-color: $gray-lt;
          border: 5px solid $gray-border;
          position: absolute;
          padding: 1rem 2rem;
          top: 0;
          right: 0;
          left: 0;
          bottom: 0;
          z-index: 2;
          p {
            font-size: 1rem;
            margin-bottom: 2rem;
          }
          .mg_btn {
            width: 10rem;
          }
        }
        .item {
          background-color: $white;
          box-shadow: 0 2px 5px 0 rgba($gray-dk, 0.15);
          margin: 1rem 0;
          padding: 1rem;
          .coin_wrapper {
            column-gap: 1rem;
            display: grid;
            align-items: stretch;
            justify-items: stretch;
            grid-template-columns: 6rem 1fr 6rem; 
            .coin {
              
              background-color: rgba($gray-dk, 0.9);
              border: 8px solid $gray-dk;
              border-radius: 50%;
              display: grid;
              align-items: center;
              justify-items: center;
              grid-template-columns: 1fr; 
              height: 4rem;
              text-align: center;
              width: 4rem;
              .logo {
                background: url('../assets/images/mini-logo-white.png');
                background-size: cover;
                filter: drop-shadow(3px 3px 4px $gray-dk);
                height: 3.5rem;
                width: 3.5rem;
                position: relative;
                top: .1rem;
                left: .1rem;
              }
              &.green {
                box-shadow: 1px 2px 0 2px $green;
              }
              &.orange {
                box-shadow: 1px 2px 0 2px $orange;
              }
              /*i {
                color: $gray-lt;
                filter: drop-shadow(3px 3px 4px $gray-dk);
                font-size: 2.3rem;
                line-height: 4rem;
              }*/
            }
            .name_wrapper {
              h4 {
                font-size: 1.25rem;
                margin: .8rem 0 0;
              }
              h5 {
                color: $text-secondary;
                font-size: 1rem;
                font-weight: 500;
                margin: 0;
              }
            }
            .mg_btn {
              margin-top: 1.5rem;
            }
            .apr {
              color: $text-secondary;
              font-size: 1.2rem;
              font-weight: 500;
            }
          }
          .coin_body {
            column-gap: 1rem;
            display: grid;
            align-items: stretch;
            justify-items: stretch;
            grid-template-columns: 1fr 1fr; 
            margin: 1rem 0;
            .section {
              background-color: rgba($purple, 0.1);
              border-radius: 4px;
              padding: 1rem;
              h3 {
                color: $text-secondary;
                font-size: 1.1rem;
                font-weight: 500;
                margin: 0;
              }
              h2 {
                color: $purple-dk;
                font-size: 3rem;
                margin: 2rem 0;
                text-align: left;
              }
              .input_wrapper {
                column-gap: 1rem;
                display: grid;
                align-items: stretch;
                justify-items: stretch;
                grid-template-columns: 3fr 1fr; 
                input {
                  border: 3px solid $gray-border;
                  border-radius: 4px;
                  color: $purple-dk;
                  font-size: 1.2rem;
                  height: 2.1rem;
                  text-indent: .5rem;
                  width: 100%;
                  &:focus-visible {
                    box-shadow: 0 2px 5px 0 rgba($gray-dk, 0.25);
                    border-color: $purple-dk;
                    outline-color: transparent;
                  }
                  &.invalid {
                    border-color: $red;
                  }
                }
                .mg_btn {
                  margin: .3rem 0;
                }
                .lil_wider {
                  width: 4.75rem;
                }
              }
              .btn_wrapper {
                column-gap: .5rem;
                display: grid;
                align-items: stretch;
                justify-items: stretch;
                grid-template-columns: 1fr 1fr; 
                .mg_btn {
                  width: 8.3rem;
                }
              }
            }
          }
          .etherscan_btn {
            margin-top: 2rem;
            width: 10rem;
          }
        }
      }
    }
    .right_wrapper {
      background-color: rgba($purple, 0.1);
      border-radius: .3rem;
      margin-top: 1rem;
      .right_selector {
        border-bottom: 2px solid rgba($purple-dk, 0.25);
        column-gap: .5rem;
        display: grid;
        align-items: stretch;
        justify-items: stretch;
        grid-template-columns: 1fr 1fr;
        margin: 1rem 2rem 1.5rem;
        padding: .5rem 1rem;
        h3 {
          border-radius: 4px;
          color: $gray-dk;
          cursor: pointer;
          font-size: 1rem;
          font-weight: 600;
          line-height: 2.25rem;
          margin: 0;
          text-align: center;
          transition: .1s ease-in-out background-color;
          &.selected {
            background-color: rgba($purple-dk, 0.15);
          }
        }
      }
      .instructions_wrapper {
        .instruction {
          column-gap: 1rem;
          display: grid;
          align-items: stretch;
          justify-items: stretch;
          grid-template-columns: 1.5rem 2fr;
          margin: 1rem;   
          div {
            //background: linear-gradient(-60deg, var(--ac-2), var(--ac-1));
            background-color: $purple;
            border-radius: 50%;
            color: $gray-lt;
            font-size: .9rem;
            height: 1.5rem;
            line-height: 1.5rem;
            text-align: center;
            width: 1.5rem;
          }
          p {
            color: $gray-dk;
            font-size: .95rem;
            margin: 0;
          }
        }
      }
      .about_wrapper {
        color: $gray-dk;
        margin: 1rem 2rem;  
        p {
          margin: .2rem 0;
          font-size: .95rem;
          span {
            color: $blue;
            cursor: pointer;
            text-decoration: underline;
          }
        }
      }
      
    }
  }  
}

</style>
